// 
export const fugleAPIGetFiveYear = (userId) => {
  return Promise.all([
    fugleAPIGet1(userId),
    fugleAPIGet2(userId),
    fugleAPIGet3(userId),
    fugleAPIGet4(userId),
    fugleAPIGet5(userId),
  ]);
};